interface IDefaultState {
    address: string,
    city: string
}


const defaultState:IDefaultState = {
    address: '2 амбулаторный проезд 17',
    city: 'Москва'
}

enum AddressActionTypes {
    getAddress = 'getAddress',
    getCity = 'getCity'
}

interface GetTariffsItem {
    type: AddressActionTypes.getAddress
    payload: string
}

interface GetCity {
    type: AddressActionTypes.getCity
    payload: string
}

type addressAction = GetTariffsItem | GetCity

export const addressReducer = (state = defaultState, action:addressAction):IDefaultState => {
    switch (action.type) {
        case AddressActionTypes.getAddress:
            return {...state, address: action.payload}

        case AddressActionTypes.getCity:
            return {...state, city: action.payload}

        default:
            return state
    }
}