import React, { FC, useEffect, useState } from 'react'
import './Address.scss'
import btnRight from '../../img/home/btnRight.svg'
import { Link } from 'react-router-dom'
import axios from 'axios'
import TableString from '../../Components/TableString/TableString'
import Pagination from '../../Components/Pagination/Pagination'
import { useTypeSelector } from '../../Store'
import { useDispatch } from 'react-redux'
import Filter from '../../Components/Filter/Filter'
import HeroTable from '../../Components/HeroTable/HeroTable'
import { useHistory } from 'react-router-dom'

const Address:FC = () => {

    const dispatch = useDispatch();

    const GetAddress = async () => {
            try {
                // const response = await axios.get(`http://212.47.227.248:12000/api/v1/tariffs?address=${street}, ${home}&limit=20&offset=30`);
                const response = await axios.get(`https://inet.qstudio.org/api/v1/tariffs?address=%D0%BC%D0%BE%D1%81%D0%BA%D0%B2%D0%B0,%20%20%D0%B0%D0%BC%D0%B1%D1%83%D0%BB%D0%B0%D1%82%D0%BE%D1%80%D0%BD%D1%8B%D0%B9%20%D0%BF%D1%80%D0%BE%D0%B5%D0%B7%D0%B4%2017&limit=20&offset=5`);
                const api = response.data;
                console.log(response);
                dispatch({type: "getTariffs", payload: response.data.tariffs})
                dispatch({type: "getProviders", payload: response.data.providers})
                dispatch({type: "GetAddress", payload: response.data.address})
                dispatch({type: "loadingTrue", payload: true})
                // history.push(`/${response.data.address}`);
                

           } catch (err) {
                alert(`Адрес не найден, попробуйте уточнить запрос (Город, улица, номер дома полностью)`);
                history.push(`/`);
                
           }
    }
    
    useEffect(() => {
        GetAddress()
    }, [])

    // Стейт для текущей отображаемой страницы
    const [currrentPage, setCurrentPage] = useState(1)
    // Количество элементов отображаемых на странице
    let tariffsPerPage = 5

    const {tariffs, loading} = useTypeSelector(state => state.table) 
    console.log(loading);

    const history = useHistory()
    // if (!loading) {
    //     history.push("/")
    // }
    

    // последний элемент
    const lastTariffsIndex = currrentPage * tariffsPerPage;
    // первый элемент
    const firstTariffsIndex = lastTariffsIndex - tariffsPerPage;
    // Текущая страница
    const cyrrentTariffs = tariffs.slice(firstTariffsIndex, lastTariffsIndex)


    const paginate = (pageNumber: number) => {setCurrentPage(pageNumber)}

    return (
        <div className="content">
            <div className="container">
            <HeroTable/>
            <Filter/>

            <section className="section home">
                <div className="container">
                    
                    <div className="section__top">
                        <h2 className="section__title">Выгодные тарифы</h2>
                        <Link to="#" className="section__close">
                            Показать все
                            <img src={btnRight} alt={btnRight} />
                        </Link>
                    </div>
                    <div className="home-block section-block"></div>
                    <div className="table">
                        <table className="table-block">
                            <tbody className="table__body">
                                <tr className="table__string">
                                    <th className="table__item">Провайдер</th>
                                    <th className="table__item">Скорость</th>
                                    <th className="table__item">Каналы</th>
                                    <th className="table__item">Моб. Связь</th>
                                    <th className="table__item">Опции</th>
                                    <th className="table__item">Стоимость</th>
                                </tr>
                                {cyrrentTariffs.map(el => <TableString key={el.name} name={el.name} provider={el.provider} speed={el.speed} price={el.price} connection_price={el.connection_price} url={el.url} pay_type={el.pay_type} pay_currency={el.pay_currency} tv_services={el.tv_services}/>)}
                            </tbody>
                        </table>
                    </div>
                </div>
            </section>

            <Pagination currrentPage={currrentPage} paginate={paginate} tariffsPerPage={tariffsPerPage}/>

            <section className="section">
                <div className="container">
                    <div className="section__top">
                    <h2 className="section__title">На ул Ленина 1 услуги связи оказывают несколько крупных и надежных провайдеров</h2>
                </div>
                <div className="section-block">
                    <p className="section__text">
                        С помощью нашего сервиса вы сможете подобрать оптимальные условия сотрудничества или воспользоваться специальными предложениями наших партнёров
                        Доступны для подключения как отдельные услуги, так и пакеты «Интернет + Цифровое ТВ»
                    </p>
                    <p className="section__text">
                        Эксклюзивные цены на связь для жителей на ул Ленина 1
                    </p>
                    <p className="section__text">
                        Бесплатная консультация по телефону, помощь в подборе тарифа и оформление заявок на подключение
                    </p>
                </div>
                </div>
            </section>

            <section className="section address">
                <div className="container">
                    <div className="section__top">
                        <h2 className="section__title">Подключенные дома по адресу Ленина ул:</h2>
                    </div>
                    <div className="section-block ">
                      <ul className="address__list">
                          <li className="address__link">1</li>
                          <li className="address__link">1 к5</li>
                          <li className="address__link">3</li>
                          <li className="address__link">8</li>
                      </ul>
                    </div>
                </div>
            </section>
            </div>
    </div>
    )
}

export default Address
