interface IDefaultState {
    provider: string
}


const defaultState:IDefaultState = {
    provider: '',
}

enum SelectActionTypes {
    selectProvider = 'selectProvider',
}

interface GetTariffsItem {
    type: SelectActionTypes.selectProvider
    payload: string
}

type SelectAction = GetTariffsItem 

export const selectReducer = (state = defaultState, action:SelectAction):IDefaultState => {
    switch (action.type) {
        case SelectActionTypes.selectProvider:
            return {...state, provider: action.payload}

        default:
            return state
    }
}