import React, { FC, MouseEvent, useState,  } from 'react'
import './SearchForm.scss'
import location from '../../img/searchForm/location.svg'
import axios from 'axios'
import { useDispatch } from 'react-redux'
import { Link, useHistory } from 'react-router-dom'
import { useTypeSelector } from '../../Store'

const SearchForm:FC = () => {

    const dispatch = useDispatch();
    const {address, city} = useTypeSelector(state => state.address)

    const [street, setStreet] = useState<string>('')
    const [home, setHome] = useState<string>('')
    const history = useHistory()

    async function getTariffs(e: MouseEvent<HTMLFormElement>) {
        e.preventDefault()
        // if (address !== '' && city !== '') {
        
                history.push(`/${address}`);
            
        // }
        
    }

    return (
        <form action="#" className="form search" onSubmit={getTariffs}>
            <label className="search__wrapper search__street">
                <img className="search__img" src={location} alt={location} />
                <input onChange={(e) => dispatch({type: 'getAddress', payload: e.target.value})} list="street" className="form__input search__input " placeholder="Введите улицу" />
                <datalist id="street">
                </datalist>
            </label>
            <label className="search__wrapper search__home">
                <input value={home} onChange={(e) => setHome(e.target.value)} list="home" className="form__input search__input" placeholder="Дом" />
                <datalist id="home">
                </datalist>
            </label>
            <label className="search__wrapper search__type">
                <input onChange={(e) => dispatch({type: 'getCity', payload: e.target.value})} list="type" className="form__input search__input" placeholder="Город"/>
                <datalist id="type">
                </datalist>
            </label>
            {/* <Link to="/city"> <input className="search__btn btn" type="submit" value="Показать тарифы" /></Link> */}
            <input className="search__btn btn" type="submit" value="Показать тарифы" />
        </form>
    )
}

export default SearchForm
